import React, { useState } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption
} from 'reactstrap';
import MyCard from '../MyCard/MyCard';
import UnderlineHeading from '../UnderlineHeading/UnderlineHeading';
import "./Home.scss";

import c1 from "../../Assets/Image/Home-Images/c_1.jpg"
import c3 from "../../Assets/Image/Home-Images/c_6.jpeg"
import c2 from "../../Assets/Image/Home-Images/c_7.jpg" 
import HomeData from "../../Assets/Data/HomePageData";


// ----------------------------------
const items = [
  {
    src: c1,
    altText: 'Nandani',
    caption: 'Nandani',
    text: 'The Magazine'
  },
  {
    src: c2,
    altText: 'Slide 2',
    caption: 'Slide 2',
    text: 'The Magazine'
  },
  {
    src: c3,
    altText: 'Slide 3',
    caption: 'Slide 3',
    text: 'The Magazine'
  }
];


const Home = (props) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  }

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  }

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  }

  const slides = items.map((item) => {
    return (
    
          <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={item.src}
      >
        <img id="img" src={item.src} alt={item.altText} />
        <CarouselCaption captionText={item.text} captionHeader={item.caption} />
      </CarouselItem>
   
    );
  });

  return (
      <>
    <div className="Home" >
        <Carousel
      activeIndex={activeIndex}
      next={next}
      previous={previous}
    >
      <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={goToIndex} />
      {slides}
      <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
      <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
    </Carousel>
    </div>
    {/*  */}
    <div className="HomeInfo"  >
        <UnderlineHeading Title="Best Seller Magazines" />
        <div className="col-11 mx-auto HomeInfoCard " >

        {
            HomeData.map((val,idx)=>{
              return(
                <MyCard 
                key = {idx}
                image = {val.image}
                title = {val.title}
                price = {val.price}
                button = {val.button} />
              )
            })
        }
        </div>
    </div>
    </>
  );
}

export default Home;
